import { isPlatformBrowser } from '@angular/common';
import { AfterViewInit, Component, ElementRef, Inject, OnInit, PLATFORM_ID, signal, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { AgentsApi } from '@core/data-access/apis/agents.api';
import { AuthService } from '@core/data-access/services/auth.service';
import { ProfileState } from '@core/data-access/states/profile.state';
import { PremiumUnlimitedPlan } from '@core/enums';
import { SnackService } from '@core/services/snack.service';
import { AppEmailValidator } from '@core/validators';
import { ButtonType } from '@shared/components/button/button.component';
import { AgentLandingFacade } from './agent-landing.facade';

@Component({
	selector: 'findqo-agent-landing',
	standalone: false,
	templateUrl: './agent-landing.component.html',
	styleUrl: './agent-landing.component.scss',
})
export class AgentLandingComponent implements OnInit, AfterViewInit {
	constructor(
		@Inject(PLATFORM_ID) private platformId: string,
		private fb: FormBuilder,
		private dialog: MatDialog,
		private snack: SnackService,
		private agentLandingFacade: AgentLandingFacade,
		private agentsApi: AgentsApi,
		private readonly snackService: SnackService,
		private readonly router: Router,
		private readonly authService: AuthService,
		private readonly profileState: ProfileState
	) {}

	@ViewChild('scrollTrigger', { read: ElementRef }) scrollTrigger: ElementRef;
	@ViewChild('adListTemplate', { read: ElementRef }) adListTemplate: ElementRef;

	form: FormGroup = this.fb.group({
		name: ['', [Validators.required, Validators.maxLength(80), Validators.minLength(3)]],
		email: ['', [Validators.required, AppEmailValidator.validateEmailPattern()]],
		phone: ['', [Validators.required, Validators.maxLength(10), Validators.pattern('^[0-9]*$')]],
		companyName: [''],
	});

	btnType = ButtonType;
	data = this.agentLandingFacade.getAgentLandingData$;
	metaData = this.agentLandingFacade.getAgentLandingMetaData$;
	pageNumber: number = 1;
	submitLoading: boolean = false;
	loadingAds: boolean = false;
	paymentPlan = signal(PremiumUnlimitedPlan.MONTHLY);

	isBrowser = isPlatformBrowser(this.platformId);

	private intersectionObserver?: IntersectionObserver;

	private initIntersectionObserver(): void {
		if (isPlatformBrowser(this.platformId) && this.adListTemplate?.nativeElement) {
			this.intersectionObserver = new IntersectionObserver(
				(entries) => {
					entries.forEach((entry: IntersectionObserverEntry) => {
						if (entry.isIntersecting) {
							this.fetchAdsOnScroll();
						}
					});
				},
				{ rootMargin: '0px', threshold: 0.1 }
			);

			this.intersectionObserver.observe(this.scrollTrigger.nativeElement);
		}
	}

	private fetchAdsOnScroll(): void {
		const PAGE_META_DATA = this.agentLandingFacade.getMetaData();

		if (+PAGE_META_DATA?.totalPages > this.pageNumber) {
			this.loadingAds = true;
			this.pageNumber += 1;
			this.agentsApi
				.getList({
					page: this.pageNumber,
				})
				.subscribe((res) => {
					this.loadingAds = false;
					this.agentLandingFacade.setAgentData([...this.agentLandingFacade?.getAgentData(), ...res.data]);
					this.agentLandingFacade.setMetaData(res.meta);
				});
		}
	}

	onTogglePaymentPlan(): void {
		this.paymentPlan.set(this.paymentPlan() === PremiumUnlimitedPlan.MONTHLY ? PremiumUnlimitedPlan.YEARLY : PremiumUnlimitedPlan.MONTHLY);
	}

	ngAfterViewInit(): void {
		this.initIntersectionObserver();
	}

	ngOnInit(): void {
		if (!this.agentLandingFacade?.getAgentData()?.length && this.isBrowser) {
			this.agentLandingFacade.getAgentLandingList();
		}
	}
}
