import { Injectable } from '@angular/core';
import { AgentsApi } from '@core/data-access/apis/agents.api';
import { AgentLandingItem, AgentSignUpBody } from '@core/models';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
	providedIn: 'root',
})
export class AgentLandingFacade {
	constructor(private readonly agentApi: AgentsApi) {}

	private _agentLandingData$ = new BehaviorSubject<AgentLandingItem[]>(null);
	private _agentLandingMetaData$ = new BehaviorSubject<{
		currentPage: string;
		perPage: string;
		total: string;
		totalPages: string;
	}>(null);

	get getAgentLandingData$(): Observable<AgentLandingItem[]> {
		return this._agentLandingData$.asObservable();
	}

	get getAgentLandingMetaData$(): Observable<{
		currentPage: string;
		perPage: string;
		total: string;
		totalPages: string;
	}> {
		return this._agentLandingMetaData$.asObservable();
	}

	getAgentData(): AgentLandingItem[] {
		return this._agentLandingData$.getValue();
	}

	getMetaData(): {
		currentPage: string;
		perPage: string;
		total: string;
		totalPages: string;
	} {
		return this._agentLandingMetaData$.getValue();
	}

	setAgentData(data: AgentLandingItem[]) {
		return this._agentLandingData$.next(data);
	}

	setMetaData(metaData: { currentPage: string; perPage: string; total: string; totalPages: string }) {
		this._agentLandingMetaData$.next(metaData);
	}

	singupAgent(body: AgentSignUpBody): Observable<object> {
		return this.agentApi.agentSignUp(body);
	}

	getAgentLandingList(params?: { page: number }) {
		this.agentApi.getList(params).subscribe((res) => {
			this._agentLandingData$.next(res?.data);
			this._agentLandingMetaData$.next(res?.meta);
		});
	}
}
