import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AgentLandingComponent } from './agent-landing.component';
import { AGENT_SIGNUP_META_CONTENTS, APP_ROUTE } from '@core/constants';
import { CanonicalGuard } from '@core/guards/seo/canonical.guard';
import { SeoGuard } from '@core/guards/seo/seo.guard';

const routes: Routes = [
  {
    path: '',
    component: AgentLandingComponent,
  },
  {
    path: APP_ROUTE.agentHub.agentSignup,
    redirectTo: '',
    data: { title: AGENT_SIGNUP_META_CONTENTS.title, description: AGENT_SIGNUP_META_CONTENTS.description },
    loadChildren: () => import('../agent/agent.module').then((m) => m.AgentModule),
    canActivate: [SeoGuard, CanonicalGuard],

  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AgentLandingRoutingModule {
}
